import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Carousel} from 'antd'
import { carouselTitleAction } from './../../redux/actions/carouselTitleAction';

//学校
import Dangyuanshuliang from './component/Dangyuanshuliang'
import Zhuanyeqingkuang from './component/Zhuanyeqingkuang'
import Quzhenqinkuang from './component/Quzhenqinkuang'
import XueshenZongRenShu from './component/XueshenZongRenShu'
import BiyeLv from './component/BiyeLv'
import JiuYeLv from './component/JiuYeLv'
import JiuYeMianXiang from './component/JiuYeMianXiang'
import BenDiJiuYeLv from './component/BenDiJiuYeLv'
import YanJiuShenJiaoShi from './component/YanJiuShenJiaoShi'
import JianZhiJiaoShi from './component/JianZhiJiaoShi'
import JianKeJiaoShi from './component/JianKeJiaoShi'
import ShuangShiSuZhiBiLi from './component/ShuangShiSuZhiBiLi'
import FuDaoYuanShenShiBi from './component/FuDaoYuanShenShiBi'
import XiaoQiHeZuoJiaoCai from './component/XiaoQiHeZuoJiaoCai'
import XiaoQiHeZuoBiYeShenBiLv from './component/XiaoQiHeZuoBiYeShenBiLv'
import XiaoQiHeZuoKeChengZongShu from './component/XiaoQiHeZuoKeChengZongShu'
import KeYanDaoKuan from './component/KeYanDaoKuan'
import KeYanXiangMuZongShu from './component/KeYanXiangMuZongShu'
import LingDaoBanZi from './component/LingDaoBanZi'
import ShenJunShiZhanChangSuo from './component/ShenJunShiZhanChangSuo'
import WenMingXueShenShuLiang from './component/WenMingXueShenShuLiang'
import ZhuanYeKeChengXueShiShu from './component/ZhuanYeKeChengXueShiShu'

class Xiangcha extends Component {
  render() {
    let autoplaySpeed = 40000;
        const settings = {
            // autoplay: true,
            autoplaySpeed,
            dots: true,
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            vertical: true,
            verticalSwiping: true,
            beforeChange: function (currentSlide, nextSlide) {
                this.props.changeCarouselTitle(nextSlide);
            },
            afterChange: (currentSlide) => {
                
                if (currentSlide === 4) {
                    setTimeout(() => {
                        if (this.slider && this.slider.goTo) {
                            this.slider.goTo(0)
                        }
                    }, autoplaySpeed)
                }
            },
        };
    return (
      <Carousel  ref={slider => (this.slider = slider)} {...settings} >
        {/* 学校 */}
        <div>
          <div style={{ height:'calc(100vh - 74px)',overflowY:'auto'}}>          
            <Dangyuanshuliang></Dangyuanshuliang>
            <Zhuanyeqingkuang></Zhuanyeqingkuang>
            <Quzhenqinkuang></Quzhenqinkuang>
            <XueshenZongRenShu></XueshenZongRenShu>
            <BiyeLv></BiyeLv>
            <JiuYeLv></JiuYeLv>
            <JiuYeMianXiang></JiuYeMianXiang>
            <BenDiJiuYeLv></BenDiJiuYeLv>
            <YanJiuShenJiaoShi></YanJiuShenJiaoShi>
            <JianZhiJiaoShi></JianZhiJiaoShi>
            <JianKeJiaoShi></JianKeJiaoShi>
            <ShuangShiSuZhiBiLi></ShuangShiSuZhiBiLi>
            <FuDaoYuanShenShiBi></FuDaoYuanShenShiBi>
            <XiaoQiHeZuoJiaoCai></XiaoQiHeZuoJiaoCai>
            <XiaoQiHeZuoBiYeShenBiLv></XiaoQiHeZuoBiYeShenBiLv>
            <XiaoQiHeZuoKeChengZongShu></XiaoQiHeZuoKeChengZongShu>
            <KeYanDaoKuan></KeYanDaoKuan>
            <KeYanXiangMuZongShu></KeYanXiangMuZongShu>
            <LingDaoBanZi></LingDaoBanZi>
            <ShenJunShiZhanChangSuo></ShenJunShiZhanChangSuo>
            <WenMingXueShenShuLiang></WenMingXueShenShuLiang>
            <ZhuanYeKeChengXueShiShu></ZhuanYeKeChengXueShiShu>
          </div>
        </div>

        {/* 每一页的划分(专业维度-向差) */}
        
      </Carousel>
    )
  }
}

export default connect(null, 
  (dispatch) => {
      return {
          changeCarouselTitle(type){
              const action = carouselTitleAction(type);
              dispatch(action);
          }
      }
  })(Xiangcha)
